.orderDetailsPage {
  background-color: #fff;
}

.orderDetailsContainer > h1 {
  font: 300 3vmax "Roboto";
  margin: 4vmax 0;
  color: tomato;
}

.orderDetailsContainer {
  padding: 5vmax;
  padding-bottom: 0;
}

.orderDetailsContainer > p {
  font: 400 1.8vmax "Roboto";
}

.orderDetailsContainerBox,
.orderDetailsCartItemsContainer {
  margin: 2vmax;
}

.orderDetailsContainerBox > div {
  display: flex;
  margin: 1vmax 0;
}

.orderDetailsContainerBox > div > p {
  font: 400 1vmax "Roboto";
}

.orderDetailsContainerBox > div > span {
  margin: 0 1vmax;
  font: 100 1vmax "Roboto";
  color: #575757;
}

.orderDetailsCartItems > p {
  font: 400 1.8vmax "Roboto";
}

.orderDetailsCartItems {
  padding: 2vmax 5vmax;
  margin: 5vmax;
  transition: all 0.5s;
}

.orderDetailsCartItems:hover{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}

.orderDetailsCartItemsContainer > div {
  display: flex;
  font: 400 1vmax "Roboto";
  align-items: center;
  justify-content: space-between;
  margin: 2vmax 0;
}

.orderDetailsCartItemsContainer > div > a > img {
  height: 5vmax;
  margin-right: 3vmax;
}

.orderDetailsCartItemsContainer > div > a {
  color: #575757;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font: 100 1.5vmax "Roboto";
}

.orderDetailsCartItemsContainer > div > span {
  font: 100 1.5vmax "Roboto";
  color: #5e5e5e;
}

@media screen and (max-width: 600px) {
  .orderDetailsContainer > p {
    font: 400 6vw "Roboto";
  }
  .orderDetailsContainerBox > div {
    margin: 6vw 0;
  }
  .orderDetailsContainerBox > div > p {
    font: 400 4vw "Roboto";
  }
  .orderDetailsContainerBox > div > span {
    font: 100 4vw "Roboto";
  }
  .orderDetailsCartItems > p {
    font: 400 6vw "Roboto";
  }
  .orderDetailsCartItemsContainer > div {
    font: 400 4vw "Roboto";
    margin: 4vw 0;
  }

  .orderDetailsCartItems{
    margin: 0;
  }
  
  .orderDetailsCartItemsContainer > div > a > img {
    width: 10vmax;
  }

  .orderDetailsCartItemsContainer > div > a {
    font: 100 4vw "Roboto";
  }

  .orderDetailsCartItemsContainer > div > span {
    font: 100 4vw "Roboto";
  }
}
